.header{
    width: 100%;
    height: 64px;
    background: #032541;
}
.header .inner{
    width: 1300px;
    height: 100%;
    display: flex;
    align-items: center;
    margin: auto;
    padding: 0 45px 0 40px;
    position: relative;
}
.inner h1 a img{
    height: 20px;
    margin-bottom: 3px;
    margin-right: 24px;
}
.ri_gnb{
    display: flex;
    gap: 33px;
    margin-right: 476px;
}
.left_gnb{
    display: flex;
    gap: 33px;
}
.left_gnb li:last-child{
    display: block;
}
a{
    color: #fff;
}
.left_gnb li:nth-child(2) a{
    font-size: 12px;
    font-weight: 700;
    padding: 4px 6px;
    border: 1px solid #fff;
    border-radius: 4px;
}
.left_gnb li:nth-child(2) a:hover{
    background: #fff;
    color: #032541;
    transition: all ease-in-out 150ms;
}
.left_gnb li:last-child a img{
    height: 22px;
}

