.banner{
    width: 100%;
    height: 406px;
    background: #032541;
}
.inner{
    margin: auto;
    margin-left: 346px;
}
.banner .search1{
    width: 100%;
    height: 48px;
    border-top: 1px solid #DCE5EC;
    border-bottom: 1px solid #DCE5EC;
    background: #fff;
    margin-bottom: 92px;
}
.banner .search1 input{
    margin-left: 346px;
}
.banner .search1 img{
    display: block;
    height: 16px;
}
.banner .search1 input::placeholder{
    font-style: italic;
}
input{
    outline: none;
    width: 1218px;
    height: 46px;
    border: none;
}
.banner p{
    font-size: 30px;
    font-weight: 600;
    line-height: 32px;
    color: #fff;
    margin-bottom: 52px;
}
.banner span{
    font-size: 46px;
    font-weight: 700;
}
.search2 input{
    border-radius: 999px;
    border: 1px solid #DCE5EC;
}
.search2 input::placeholder{
    text-indent: 15px;
    font-size: 16px;
}
