.footer{
    width: 100%;
    height: 325px;
    margin: auto;
    background: #031F37;
}
.wrap{
    width: 100%;
    height: 100%;
    display: flex;
    gap: 45px;
    justify-content: center;
    align-items: center;
}
.footer_left ul li:last-child a{
    color: #0FB5DD;
    background: #fff;
    border-radius: 6px;
    padding: 12px 18px;
    display: block;
    font-size: 18px;
    font-weight: 700;
    margin-top: 46px;
}
.footer_ri{
    display: flex;
    gap: 50px;
}
.footer_ri ul li{
    cursor: pointer;
    color: #fff;
}
.footer_ri ul li:first-child{
    font-size: 18px;
    font-weight: 700;
    cursor: default;
}