.allmovie{
    width: 100%;
    margin-bottom: 35px;
}
.allmovie .inner{
    width: 1300px;
}
.allmovie h2{
    font-size: 18px;
    margin-bottom: 25px;
    margin-top: 35px;
}
.imgbox{
    width: 150px;
    position: relative;
}
.imgbox img{
    width: 100%;
    height: 225px;
    margin-bottom: 30px;
    border-radius: 8px;
    cursor: pointer;
}
.imgbox .more{
    width: 20px;
    height: 20px;
    position: absolute;
    right: 11px;
    top: 11px;
    opacity: 0.5;
}
.imgbox .more:hover{
    opacity: 1;
}
.poster h4{
    font-size: 16px;
    font-weight: 700;
}
.poster p{
    font-size: 14px;
    color: #999;
}

